import React from 'react';
import { connect } from "react-redux"
import NavListItem from "./NavListItem"
import MainMenuSlide3Sublist from "./MenuSubList"

const MainMenuNav = (props) => {
	return (
		<div className="main-menu__nav">
			<div className="main-menu__nav-list">
				<NavListItem
					slideIndex={ 1 }
					slideTitle=" "
				/>
				<NavListItem
					slideIndex={ 2 }
					slideTitle={ props.slide2.title }
				/>
				<NavListItem
					slideIndex={ 4 }
					slideTitle={ props.slide4.title }
				/>
				<NavListItem
					slideIndex={ 3 }
					slideTitle={ props.slide3.title }
				>
					<MainMenuSlide3Sublist />
				</NavListItem>
			</div>
			
			<div className="main-menu__copyright">
				<div className="copyright__company">
					&copy; { new Date().getFullYear() } 
				</div>
				
				<div className="copyright__developer">
					<span>  </span>
					
					<a href="//mways.ru" target="_blank" rel="noopener noreferrer">mways.ru</a>
				</div>
			</div>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi,
	...state.appData
})

export default connect(mapStateToProps)(MainMenuNav);
