import React from 'react';
import { connect } from "react-redux"
import { changeViewport, heroTransitionToggle } from "../redux/actions/appUiActions"
import { slide4RestoreDefaults } from "../redux/actions/logisticsUiActions"

const GoBackBtn = (props) => {
	const goBackBtnHandleClick = () => {
		if (props.heroTransitionDisabled) {
			props.dispatch(heroTransitionToggle())
			props.dispatch(changeViewport("mainScreen"))
			
			setTimeout(() => {
				props.dispatch(heroTransitionToggle())
			}, 1000)
			
			setTimeout(() => {
				props.dispatch(slide4RestoreDefaults())
				document.querySelector('.slide-content').scrollTo({ left: 0 })
			}, 800)
		}
	}
	
	const classActive = (props.currentViewport === "slideContent") ? " go-back-btn--active" : ""
	
	return (
		<button
			className={ "go-back-btn" + classActive }
			onClick={ goBackBtnHandleClick }
		>
			GoBack
		</button>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(GoBackBtn);
