import React from 'react';
import { connect } from "react-redux"

const IndicatorItem = (props) => {
	const indicatorActive = (props.indicatorId === props.currentSlideId) ? " indicator__item--active" : ""
	
	return (
		<div
			className={"indicator__item" + indicatorActive}
			data-indicatorid={ props.indicatorId }
		>
			[I]
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(IndicatorItem);