// Logistics UI actions


// SECTION_NEXT
const slide4SectionNext = () => ({
	type: 'SECTION_NEXT'
})

// SECTION_PREV
const slide4SectionPrev = () => ({
	type: 'SECTION_PREV'
})



// Change nav items for each section
// section nav item change
// SECTION_NAV_NEXT_ITEM
const sectionNavNextItem = (sectionIndex) => ({
	type: 'SECTION_NAV_NEXT_ITEM',
	sectionIndex
})
// SECTION_NAV_PREV_ITEM
const sectionNavPrevItem = (sectionIndex) => ({
	type: 'SECTION_NAV_PREV_ITEM',
	sectionIndex
})

// section nav position change
// SECTION_NAV_MOVE_RIGHT
const sectionNavMoveRight = (sectionIndex) => ({
	type: 'SECTION_NAV_MOVE_RIGHT',
	sectionIndex
})
// SECTION_NAV_MOVE_LEFT
const sectionNavMoveLeft = (sectionIndex) => ({
	type: 'SECTION_NAV_MOVE_LEFT',
	sectionIndex
})

// NAV_ENABLED_TOGGLE
const navEnabledToggle = () => ({
	type: 'NAV_ENABLED_TOGGLE'
})

// SLIDE_4_RESTORE_DEFAULTS
const slide4RestoreDefaults = () => ({
	type: 'SLIDE_4_RESTORE_DEFAULTS'
})




export {
	slide4SectionNext,
	slide4SectionPrev,
	sectionNavNextItem,
	sectionNavPrevItem,
	sectionNavMoveRight,
	sectionNavMoveLeft,
	
	navEnabledToggle,
	
	slide4RestoreDefaults,
}