import React from 'react';
import ReactDOM from 'react-dom';
// import 'normalize.css'
import './scss/main.scss'
import App from './App';
import * as serviceWorker from './serviceWorker';
import configureStore from "./redux/store/configureStore"
import { Provider } from "react-redux"

import ScrollMagic from 'scrollmagic'

const store = configureStore()

const jsx = (
	<Provider store={ store }>
		<React.StrictMode>
			<App />
		</React.StrictMode>
	</Provider>
)

ReactDOM.render(jsx, document.getElementById('root'));

const revealItems = () => {
	const controller = new ScrollMagic.Controller({ vertical: false })
	const magicFunc = (item) => {
		new ScrollMagic.Scene({
			triggerElement: item,
			offset: -20,
			triggerHook: 0.9,
			reverse: true
		})
			.setClassToggle(item, "visible")
			.addTo(controller)
	}
	
	
	const revealToLeft = document.querySelectorAll('.reveal--to-left')
	revealToLeft.forEach(item => {
		magicFunc(item)
	})
	
	const revealToRight = document.querySelectorAll('.reveal--to-right')
	revealToRight.forEach(item => {
		magicFunc(item)
	})
	
	const revealToTop = document.querySelectorAll('.reveal--to-top')
	revealToTop.forEach(item => {
		magicFunc(item)
	})
	
	const revealToBot= document.querySelectorAll('.reveal--to-bot')
	revealToBot.forEach(item => {
		magicFunc(item)
	})
	
	const revealFade= document.querySelectorAll('.reveal--fade')
	revealFade.forEach(item => {
		magicFunc(item)
	})
}

revealItems()

setTimeout(() => {
	// revealItems()
}, 1000)

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();