import React from 'react';
import { connect } from "react-redux"
import IndicatorItem from "./SliderIndicator/IndicatorItem"

const SliderIndicator = (props) => {
	return (
		<div className="slider__indicator">
			<IndicatorItem indicatorId={1} />
			<IndicatorItem indicatorId={2} />
			<IndicatorItem indicatorId={3} />
			<IndicatorItem indicatorId={4} />
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(SliderIndicator);
