import React from 'react';
import { connect } from "react-redux"

const Slide4SectionNavIndicator = (props) => {
	
	
	const indicatorPosStyles = {
		left: `calc(((100vw - 80px) / 3) * ${ props.logistics[`s${props.sectionIndex}CurrentItem`] - props.logistics[`s${props.sectionIndex}leftItemIndex`] })`,
	}
	
	return (
		<div
			className="s4__nav-indicator"
			style={ indicatorPosStyles }
		>
			<span />
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide4SectionNavIndicator);