// App UI actions

// TOGGLE_MENU
const toggleMenu = () => ({
	type: 'TOGGLE_MENU'
})

// TOGGLE_MENU
const closeMenu = () => ({
	type: 'CLOSE_MENU'
})

// CHANGE_VIEWPORT
const changeViewport = (viewportName) => ({
	type: 'CHANGE_VIEWPORT',
	viewportName
})

// CHANGE_SLIDE
const changeSlide = (newSlideId) => ({
	type: 'CHANGE_SLIDE',
	newSlideId
})

// CHANGE_PRIMARY_COLOR
const changePrimaryColor = (newPrimaryColor) => ({
	type: 'CHANGE_PRIMARY_COLOR',
	newPrimaryColor
})

// MAIN_SCREEN_WHEEL_SLIDE_CHANGE_TOGGLE
const mainScreenWheelSlideChangeToggle = () => ({
	type: 'MAIN_SCREEN_WHEEL_SLIDE_CHANGE_TOGGLE'
})

// HERO_TRANSITION_TOGGLE
const heroTransitionToggle = () => ({
	type: 'HERO_TRANSITION_TOGGLE'
})

// SLIDE_3_CHANGE_CHAPTER
const slide3ChangeChapter = (chapterId) => ({
	type: 'SLIDE_3_CHANGE_CHAPTER',
	chapterId
})

export {
	toggleMenu,
	changeViewport,
	changeSlide,
	changePrimaryColor,
	closeMenu,
	mainScreenWheelSlideChangeToggle,
	heroTransitionToggle,
	slide3ChangeChapter
}