import React from 'react';
import { connect } from "react-redux"
import { toggleMenu } from "../redux/actions/appUiActions"

const MenuBtn = (props) => {
	const menuBtnHandleClick = () => {
		props.dispatch(toggleMenu())
	}
	
	const menuBtnClose = props.menuOpen ? " menu-btn--close" : ""
	
	const handleKeyDown = (e) => {
		if ((e.keyCode === 27) && (props.menuOpen)) {
			props.dispatch(toggleMenu())
		}
	}
	
	return (
		<div className="menu-btn-block">
			<button
				className={ "menu-btn" + menuBtnClose }
				id="menu-btn"
				onClick={ menuBtnHandleClick }
				onKeyDown={ handleKeyDown }
				tabIndex={ 0 }
			>
				<div className="menu-btn__wrapper">
					<span className="menu-btn__line menu-btn__line--1" />
					<span className="menu-btn__line menu-btn__line--2" />
					<span className="menu-btn__line menu-btn__line--3" />
				</div>
			</button>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(MenuBtn);