import React from 'react';
import { connect } from "react-redux"
import { slide3ChangeChapter } from "../../../redux/actions/appUiActions"

const Slide3NavNavItem = (props) => {
	const handleNavItemClick = () => {
		props.dispatch(slide3ChangeChapter(props.index))
	}
	
	const itemExtraStyles = (props.appUi.slide3CurrentChapter === props.index) ? " slide-3-nav__active-item" : ""
	
	return (
		<div className={ "slide-3__nav-item" + itemExtraStyles }>
			<button
				className="s3__nav-item__btn"
				data-slideid={ props.index }
				onClick={ handleNavItemClick }
			>
				{ props.text }
			</button>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide3NavNavItem);
