import s1Img2 from "../../static/images/slide4/section-1/item-2.png"
import s1Img3 from "../../static/images/slide4/section-1/item-3.png"
// section 1
// section 2
// section 3

//      0 ( JS).       ,         
//  -->
// left: calc((100/3) * props.logistics.currentSection)

const logisticsReducerDefaultState = {
	defaultSection: 1,
	defaultLeftItemIndex: 1,
	defaultRightItemIndex: 3,
	
	navEnabled: true,
	
	currentSection: 1,
	sectionsAmount: 5,
	
	
	// Section 1
	s1CurrentItem: 1,
	s1Length: 3,
	s1leftItemIndex: 1,
	s1rightItemIndex: 3,
	
	// Section 2
	s2CurrentItem: 1,
	s2Length: 4,
	s2leftItemIndex: 1,
	s2rightItemIndex: 3,
	
	// Section 3
	s3CurrentItem: 1,
	s3Length: 4,
	s3leftItemIndex: 1,
	s3rightItemIndex: 3,
	
	// Section 4
	s4CurrentItem: 1,
	s4Length: 5,
	s4leftItemIndex: 1,
	s4rightItemIndex: 3,
	
	// Section 5 --> 1 screen map witch block
	
	
	// SectionsData
	sectionsData: {
		sections: [
			// section 1
			{
				title: " ",
				items: [
					{
						id: 1,
						text: " ",
						image: s1Img1,
					},
					{
						id: 2,
						text: "  ",
						image: s1Img2,
					},
					{
						id: 3,
						text: ",    ",
						image: s1Img3,
					},
					
				],
			},
			
			// section 2
			{
				title: " ",
				items: [
					{
						id: 1,
						text: " ,    "
					},
					{
						id: 2,
						text: "       "
					},
					{
						id: 3,
						text: "    "
					},
					{
						id: 4,
						text: " "
					},
				],
			},
			
			// section 3
			{
				title: " ",
				items: [
					{
						id: 1,
						text: " -  ,  ,    "
					},
					{
						id: 2,
						text: "  / "
					},
					{
						id: 3,
						text: "      "
					},
					{
						id: 4,
						text: "     ,     "
					},
				],
			},
			
			// section 4
			{
				title: " ",
				items: [
					{
						id: 1,
						text: "          "
					},
					{
						id: 2,
						text: "        "
					},
					{
						id: 3,
						text: "   "
					},
					{
						id: 4,
						text: "  (     )"
					},
					
					{
						id: 5,
						text: "  "
					},
				],
			},
		],
	}
}

const logisticsReducer = (
	state = logisticsReducerDefaultState, action
) => {
	switch (action.type) {
		case 'SECTION_NEXT':
			return {
				...state,
				currentSection: state.currentSection + 1
			}
		
		case 'SECTION_PREV':
			return {
				...state,
				currentSection: state.currentSection - 1
			}
			
		case 'SECTION_NAV_NEXT_ITEM':
			return {
				...state,
				[`s${action.sectionIndex}CurrentItem`]: state[`s${action.sectionIndex}CurrentItem`] + 1
			}
		
		case 'SECTION_NAV_PREV_ITEM':
			return {
				...state,
				[`s${action.sectionIndex}CurrentItem`]: state[`s${action.sectionIndex}CurrentItem`] - 1
			}
			
		case 'SECTION_NAV_MOVE_LEFT':
			return {
				...state,
				[`s${action.sectionIndex}leftItemIndex`]: state[`s${action.sectionIndex}leftItemIndex`] - 1,
				[`s${action.sectionIndex}rightItemIndex`]: state[`s${action.sectionIndex}rightItemIndex`] - 1,
			}
		
		case 'SECTION_NAV_MOVE_RIGHT':
			return {
				...state,
				[`s${action.sectionIndex}leftItemIndex`]: state[`s${action.sectionIndex}leftItemIndex`] + 1,
				[`s${action.sectionIndex}rightItemIndex`]: state[`s${action.sectionIndex}rightItemIndex`] + 1,
			}
			
		case 'NAV_ENABLED_TOGGLE':
			return {
				...state,
				navEnabled: !state.navEnabled
			}
			
		case 'SLIDE_4_RESTORE_DEFAULTS':
			return {
				...state,
				currentSection: 1,
				
				s1CurrentItem: 1,
				s1leftItemIndex: 1,
				s1rightItemIndex: 3,
				
				s2CurrentItem: 1,
				s2leftItemIndex: 1,
				s2rightItemIndex: 3,
				
				s3CurrentItem: 1,
				s3leftItemIndex: 1,
				s3rightItemIndex: 3,
				
				s4CurrentItem: 1,
				s4leftItemIndex: 1,
				s4rightItemIndex: 3,
			}
		
		default: return { ...state}
	}
}

export default logisticsReducer;