import React from 'react';
import { connect } from "react-redux"

const ItemImgSecondary = (props) => {
	
	const activeClass = (
		props.imageIndex === props.logistics[`s${ props.sectionIndex }CurrentItem`]
	) ? " s4__img-secondary--active" : ""
	
	const extraClass = (props.sgsBlue) ? " deco-sgs-blue" : ""
	
	
	return (
		<div
			className={ "s4__img--secondary" + activeClass }
		>
			<img
				src={ props.image }
				alt="Img"
			/>
			
			<div className={ "img-deco-small" + extraClass } />
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(ItemImgSecondary);