import React from 'react';
import { connect } from "react-redux"

import { useRef, useEffect } from "react";
import Rellax from "rellax";

// images
// import image_name from "../../static/images/slide2/"
import s1ImgTop from "../../static/images/slide2/img-2.jpg"
import s1ImgBottom from "../../static/images/slide2/img-1.jpg"
import s3ImgTitle from "../../static/images/slide2/img-3.jpg"
// SVG numbers
import OneSvg from "./slide3/svg-numbers/OneSvg"
import TwoSvg from "./slide3/svg-numbers/TwoSvg"
import ThreeSvg from "./slide3/svg-numbers/ThreeSvg"
import FourSvg from "./slide3/svg-numbers/FourSvg"
import FiveSvg from "./slide3/svg-numbers/FiveSvg"


const Slide2 = (props) => {
	
	const imgTopImgRef = useRef()
	const imgTopDecoRef = useRef()
	
	const imgBotImgRef = useRef()
	
	const constructionImgRef = useRef()
	
	const rellaxFunc = (elementRef, speed) => {
		new Rellax(elementRef.current, {
			speed: speed,
			round: false,
			wrapper: ".slide-content",
			horizontal: true
		});
	}
	
	useEffect(() => {
		rellaxFunc(imgTopImgRef, 2)
		rellaxFunc(imgTopDecoRef, -2)
		rellaxFunc(imgBotImgRef, -4)
		rellaxFunc(constructionImgRef, 2)
	}, []);
	
	const activeClassName = (props.appUi.currentSlideId === props.slideIndex) ? " slide--current" : " "
	
	return (
		<div
			className={
				"slide-content__slide content__slide-2" + activeClassName
			}
		>
			{/*SECTION 1*/ }
			<section className="
					slide-content__section
					slide-2--s1"
			>
				<div className="grid-child grid-child--title">
					<h2 className="reveal--to-left"> </h2>
				</div>
				<div className="grid-child grid-child--image-top reveal--to-left">
					<div
						ref={ imgTopDecoRef }
						className="image-top__deco"
					/>
					
					<div
						className="img-wrapper"
					>
						<img
							ref={ imgTopImgRef }
							src={ s1ImgTop }
							alt=""
						/>
					</div>
				</div>
				<div className="grid-child grid-child--image-bottom reveal--to-left">
					<div
						className="img-wrapper"
					>
						<img
							ref={ imgBotImgRef }
							src={ s1ImgBottom }
							alt=""
						/>
					</div>
				</div>
				
				<div className="grid-child grid-child--1 grid-child--list-item reveal--to-left">
					<div className="list-item__number">
						<OneSvg />
					</div>
					
					<div className="list-item__details">
						<p>
							 3D-      ( , ) 
							   Tekla Structures     
							21.1101-2009
						</p>
					</div>
				</div>
				
				<div className="grid-child grid-child--2 grid-child--list-item reveal--to-left">
					<div className="list-item__number">
						<TwoSvg />
					</div>
					
					<div className="list-item__details">
						<p>
							     - ,  
							  
						</p>
					</div>
				</div>
				
				<div className="grid-child grid-child--3 grid-child--list-item reveal--to-left">
					<div className="list-item__number">
						<ThreeSvg />
					</div>
					
					<div className="list-item__details">
						<p>
							  ,     
							         
							 
						</p>
					</div>
				</div>
				
				<div className="grid-child grid-child--4 grid-child--list-item reveal--to-left">
					<div className="list-item__number">
						<FourSvg />
					</div>
					
					<div className="list-item__details">
						<p>
							         
							  .
						</p>
					</div>
				</div>
				
				<div className="grid-child grid-child--5 grid-child--list-item reveal--to-left">
					<div className="list-item__number">
						<FiveSvg />
					</div>
					
					<div className="list-item__details">
						<p>
							     -,    
							      
						</p>
					</div>
				</div>
			</section>
			
			{/*SECTION 2*/ }
			
			<section className="
					slide-content__section
					slide-2--s2"
			>
				<div className="s2__title">
					<div className="title__image">
						<img ref={ constructionImgRef } src={ s3ImgTitle } alt="Title img" />
					</div>
					
					<div className="title__heading">
						<h2 className="reveal--to-left">
							 
							<br />
							
						</h2>
					</div>
				</div>
				
				<div className="s2__custom-list">
					<div className="custom-list__item list-item--1 reveal--to-top">
						<div className="custom-item__text">
							<p>
								    ,  <br />  ,   
								 , , <br /> , , , 
							</p>
							
							<div className="custom-item__deco" />
							<div className="custom-item__line" />
						</div>
					</div>
					
					<div className="custom-list__item list-item--2 reveal--to-top">
						<div className="custom-item__text">
							<p>
								, , 
							</p>
							
							<div className="custom-item__deco" />
							<div className="custom-item__line" />
						</div>
					</div>
					
					<div className="custom-list__item list-item--3 reveal--to-top">
						<div className="custom-item__text">
							<p>
								   <br />   
							</p>
							
							<div className="custom-item__deco" />
							<div className="custom-item__line" />
						</div>
					</div>
					
					<div className="custom-list__item list-item--4 reveal--to-top">
						<div className="custom-item__text">
							<p>
								, ,  <br /> , 
							</p>
							
							<div className="custom-item__deco" />
							<div className="custom-item__line" />
						</div>
					</div>
					
					<div className="custom-list__item list-item--5 reveal--to-top">
						<div className="custom-item__text">
							<p>
								  <br />     
							</p>
							
							<div className="custom-item__deco" />
							<div className="custom-item__line" />
						</div>
					</div>
					
					<div className="custom-list__item list-item--6 reveal--to-top">
						<div className="custom-item__text">
							<p>
								 
							</p>
							
							<div className="custom-item__deco" />
							<div className="custom-item__line" />
						</div>
					</div>
					
					<div className="custom-list__item list-item--7 reveal--to-top">
						<div className="custom-item__text">
							<p>
								 <br />    
							</p>
							
							<div className="custom-item__deco" />
							<div className="custom-item__line" />
						</div>
					</div>
				</div>
			</section>
		</div>
	)
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide2);
