import React from 'react';
import { connect } from "react-redux"
import Slide4Section1 from "./slide4/Section1"
import Slide4Section2 from "./slide4/Section2"
import Slide4Section3 from "./slide4/Section3"
import Slide4Section4 from "./slide4/Section4"
import Slide4Section5 from "./slide4/Section5"

// images
// import image_name from "../../static/images/slide2/"

const Slide4 = (props) => {
	
	const activeClassName = (props.appUi.currentSlideId === props.slideIndex) ? " slide--current" : " "
	
	// const testObj = {
	// 	["testString" + 1]: "test string 1"
	// }
	// console.log(testObj)
	
	const currentSectionStyles = {
		transform: `translateX(calc((100vw + 40px) * -1 * ${ props.logistics.currentSection - 1 }))`
		// transform: `translateX(calc(((100vw + 40px) * -1) * 0.1))`
	}
	
	return (
		<div
			className={
				"slide-content__slide content__slide-4" + activeClassName
			}
			style={ currentSectionStyles }
		>
			{ /*SECTION 1*/ }
			<Slide4Section1 sectionIndex={ 1 } />
			
			{ /*SECTION 2*/ }
			<Slide4Section2 sectionIndex={ 2 } />
			
			
			{ /*SECTION 3*/ }
			<Slide4Section3 sectionIndex={ 3 } />
			
			{ /*SECTION 4*/ }
			<Slide4Section4 sectionIndex={ 4 } />
			
			{ /*SECTION 5*/ }
			<Slide4Section5 sectionIndex={ 5 } />
		</div>
	)
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide4);
