import React from 'react';
import { connect } from "react-redux"
import {
	changeSlide,
	changeViewport,
	closeMenu,
	heroTransitionToggle,
	slide3ChangeChapter
} from "../../redux/actions/appUiActions"
import { slide4RestoreDefaults } from "../../redux/actions/logisticsUiActions"

const NavListSubItem = (props) => {
	const handleSubItemClick = (e) => {
		e.preventDefault()
		
		if (props.slideIndex !== 4) {
			document.querySelector('.slide-content').scrollTo({ left: 0 })
		}
		
		props.dispatch(slide4RestoreDefaults())
		
		if (props.currentSlideId !== props.slideIndex) {
			props.dispatch(changeSlide(props.slideIndex))
		}
		
		props.dispatch(slide3ChangeChapter(props.chapterIndex))
		
		if (props.currentViewport === "mainScreen" && props.heroTransitionDisabled) {
			props.dispatch(heroTransitionToggle())
			props.dispatch(changeViewport("slideContent"))
			
			setTimeout(() => {
				props.dispatch(heroTransitionToggle())
			}, 800)
		}
		
		props.dispatch(closeMenu())
	}
	
	const itemActive = ((props.slide3CurrentChapter === props.chapterIndex) && (props.currentSlideId === props.slideIndex)) ? " slide-3__active-chapter" : ""
	
	return (
		<li>
			<a
				className={ "nav-list__sub-item" + itemActive }
				href="/"
				onClick={ handleSubItemClick }
			>
				{ props.text }
			</a>
		</li>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(NavListSubItem);
