import React from 'react';
import { connect } from "react-redux"
import { changePrimaryColor, changeSlide } from "../../../redux/actions/appUiActions"

const SvgLogoPath = (props) => {
	const svgClickHandler = (e) => {
		console.log(props.index)
		
		// const svgIndex = parseInt(e.target.dataset.index, 10)
		
		if (props.currentSlideId !== props.index) {
			props.dispatch(changeSlide(props.index))
			props.dispatch(changePrimaryColor(props.color))
		}
	}
	
	const pathClass = (props.index === props.currentSlideId) ? "active-svg" : ""
	
	const styleAttr = (props.currentSlideId === props.index) ? {fill: props.color} : {}
	
	return (
		<path
			style={ styleAttr }
			className={ pathClass }
			data-index={ props.index }
			data-color={ props.color }
			d={ props.svgD }
			fill="white"
			fillOpacity="0.02"
			stroke="#000000"
			onClick={ svgClickHandler }
		/>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(SvgLogoPath);
