import React from 'react';
import { connect } from "react-redux"

const Slide4ItemImgDefault = (props) => {
	
	const activeClass = (
		props.imageIndex === props.logistics[`s${ props.sectionIndex }CurrentItem`]
	) ? " s4__item-img--active" : ""
	
	
	
	return (
		<div
			className={"s4__item-img" + activeClass }
		>
			<div className="img-wrapper">
				<img
					src={ props.image }
					alt="Img"
				/>
			</div>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide4ItemImgDefault);