import React from 'react';
import { connect } from "react-redux"
import { changeSlide, changeViewport, closeMenu, heroTransitionToggle } from "../../redux/actions/appUiActions"
import { slide4RestoreDefaults } from "../../redux/actions/logisticsUiActions"

const NavListItem = (props) => {
	const navItemHandleClick = (e) => {
		e.preventDefault()
		
		if (props.slideIndex !== 4) {
			document.querySelector('.slide-content').scrollTo({ left: 0 })
		}
		
		props.dispatch(slide4RestoreDefaults())
		
		props.dispatch(changeSlide(props.slideIndex))
		
		if (props.currentViewport === "mainScreen" && props.heroTransitionDisabled) {
			props.dispatch(heroTransitionToggle())
			props.dispatch(changeViewport("slideContent"))
			
			setTimeout(() => {
				props.dispatch(heroTransitionToggle())
			}, 800)
		}
		
		props.dispatch(closeMenu())
	}
	
	const itemActive = (props.currentSlideId === props.slideIndex) ? " menu__active-slide" : ""
	
	return (
		<>
			<a
				className={ "main-menu__nav-item" + itemActive }
				href="/"
				onClick={ navItemHandleClick }
			>
				<span>{ props.slideTitle }</span>
			</a>
			
			{ props.children }
		</>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(NavListItem);
