import React from 'react';
import { connect } from "react-redux"

import { useRef, useEffect } from "react";
import Rellax from "rellax";

// images
import heroImg from "../../../static/images/slide3/chapter-1/hero.jpg"
import S3Ch1Section1 from "./chapter-1/Section1"
import S3Ch1Section2 from "./chapter-1/Section2"
import S3Ch1Section3 from "./chapter-1/Section3"

const Section3Chapter1 = (props) => {
	
	const heroImgDecoRef = useRef();
	const heroImgRef = useRef();
	
	useEffect(() => {
		const wrapper = ".slide-content"
		new Rellax(heroImgDecoRef.current, {
			speed: -1,
			round: true,
			wrapper: wrapper,
			horizontal: true
		});
		
		new Rellax(heroImgRef.current, {
			speed: 3,
			round: true,
			wrapper: wrapper,
			horizontal: true
		});
	}, []);
	
	const currentChapterClassName = (props.appUi.slide3CurrentChapter === props.chapterId) ? " slide-3__chapter--current" : " "
	
	return (
		<div className={
			"slide-3__chapter slide-3__chapter-1" + currentChapterClassName
		}>
			<div className="
					slide-content__section
					chapter__hero"
			>
				<h2 className="reveal--to-left">
					 <br />  <br /> 
				</h2>
				
				<div className="chapter-hero__info reveal--to-left">
					<p>
						          ,
						        .
					</p>
				</div>
				
				<div
					className="chapter-hero__image chapter-hero__image--bot reveal--to-left"
				>
					<div className="chapter-hero__img-wrapper">
						<img
							ref={ heroImgRef }
							src={ heroImg }
							alt="Illustration"
						/>
					</div>
					
					<div
						ref={ heroImgDecoRef }
						className="chapter-hero__image__deco-2"
					/>
				</div>
			</div>
			
			{/* Section 1	*/ }
			<S3Ch1Section1 />
			{/* Section 2	*/ }
			<S3Ch1Section2 />
			{/* Section 3	*/ }
			<S3Ch1Section3 />
		</div>
	)
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Section3Chapter1);
