import React from 'react';
import { connect } from "react-redux"
import ReactScrollWheelHandler from "react-scroll-wheel-handler";
import {
	slide4SectionPrev
} from "../../../redux/actions/logisticsUiActions"
import mapImg from "../../../static/images/slide3/chapter-1/map.svg"


const Slide4Section5 = (props) => {
	const slideSectionUpHandler = (e) => {
		// console.log("UP")
		
		if (props.logistics.currentSection === props.sectionIndex) {
			props.dispatch(slide4SectionPrev())
		}
	}
	
	
	return (
		<ReactScrollWheelHandler
			className="s4-section s4-section--5"
			upHandler={ slideSectionUpHandler }
			disableKeyboard={ true }
		>
			<div className="map">
				<div className="map-img">
					<img className="reveal--to-bot" src={ mapImg } alt="Map" />
				</div>
				
				<p className="reveal--to-top">
					      .   : . , . , . , .     /     . .
				</p>
			</div>
			
			<div className="article__wrapper reveal--fade">
				<div className="article">
					<p>
						    ,     ,        .
					</p>
				</div>
			</div>
		
		</ReactScrollWheelHandler>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide4Section5);