import React from 'react';
import { connect } from "react-redux"
import { changeViewport, heroTransitionToggle } from "../../../redux/actions/appUiActions"
import DescriptionContent from "./DescriptionContent"

const SlideDescription = (props) => {
	
	const moreDetailsBtnHandleClick = () => {
		if (props.heroTransitionDisabled) {
			props.dispatch(heroTransitionToggle())
			props.dispatch(changeViewport("slideContent"))
			
			setTimeout(() => {
				props.dispatch(heroTransitionToggle())
			}, 800)
		}
	}
	
	const currentDescriptionBg = (props.currentSlideId === 1 || props.currentSlideId === 3) ? props.primaryColor13 : props.primaryColor24
	
	return (
		<div
			className="slider__slide-description"
			style={ { background: currentDescriptionBg } }
		>
			<DescriptionContent
				descriptionId={ 1 }
				heading={ props.slide1.title }
				paragraph={ props.slide1.description }
			/>
			
			<DescriptionContent
				descriptionId={ 2 }
				heading={ props.slide2.title }
				paragraph={ props.slide2.description }
				fz31={ true }
			/>
			
			<DescriptionContent
				descriptionId={ 3 }
				heading={ props.slide3.title }
				paragraph={ props.slide3.description }
			/>
			
			<DescriptionContent
				descriptionId={ 4 }
				heading={ props.slide4.title }
				paragraph={ props.slide4.description }
			/>
			
			<button
				id="more-details-btn"
				className="slider__more-details reveal--to-left"
				onClick={ moreDetailsBtnHandleClick }
			>
				<span></span>
			</button>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi,
	...state.appData
})

export default connect(mapStateToProps)(SlideDescription);
