// App UI reducer

const appUiReducerDefaultState = {
	menuOpen: false,
	// menuOpen: true,
	
	/* "mainScreen" or "slideContent" */
	currentViewport: "mainScreen",
	// currentViewport: "slideContent",
	
	heroTransitionDisabled: true,
	
	currentSlideId: 1,
	defaultCurrentSlideId: 1,
	
	primaryColor13: "#1061C0",
	primaryColor24: "#0095D9",
	currentPrimaryColor: "#1061C0",
	
	// Slide 3
	slide3CurrentChapter: 1,
	slide3DefaultChapter: 1,
	
	// Slide 4
	
}

const appUiReducer = (
	state = appUiReducerDefaultState, action
) => {
	switch (action.type) {
		case 'TOGGLE_MENU':
			return {
				...state,
				menuOpen: !state.menuOpen
			}
			
		case 'CLOSE_MENU':
			return {
				...state,
				menuOpen: false
			}
		
		case 'CHANGE_VIEWPORT':
			return {
				...state,
				currentViewport: action.viewportName
			}
			
		case 'CHANGE_SLIDE':
			return {
				...state,
				currentSlideId: action.newSlideId,
				slide3CurrentChapter: 1
			}
			
		case 'CHANGE_PRIMARY_COLOR':
			return {
				...state,
				currentPrimaryColor: action.newPrimaryColor
			}
			
		case 'MAIN_SCREEN_WHEEL_SLIDE_CHANGE_TOGGLE':
			return {
				...state,
				mainScreenSlideScrollActive: !state.mainScreenSlideScrollActive
			}
		
		case 'HERO_TRANSITION_TOGGLE':
			return {
				...state,
				heroTransitionDisabled: !state.heroTransitionDisabled
			}
		
		case 'SLIDE_3_CHANGE_CHAPTER':
			return {
				...state,
				slide3CurrentChapter: action.chapterId
			}
		
		default:
			return state
	}
}


export default appUiReducer;