import React from 'react';
import NavListSubItem from "./NavListSubItem"
import { connect } from "react-redux"

const MainMenuSlide3Sublist = (props) => {
	return (
		<ul className="custom-ul menu__sublist">
			<NavListSubItem
				slideIndex={ 3 }
				chapterIndex={ 1 }
				text={ props.slidesData.slide3.services.chapter1 }
			/>
			
			<NavListSubItem
				slideIndex={ 3 }
				chapterIndex={ 2 }
				text={ props.slidesData.slide3.services.chapter2 }
			/>
			
			<NavListSubItem
				slideIndex={ 3 }
				chapterIndex={ 3 }
				text={ props.slidesData.slide3.services.chapter3 }
			/>
			
			<NavListSubItem
				slideIndex={ 3 }
				chapterIndex={ 4 }
				text={ props.slidesData.slide3.services.chapter4 }
			/>
		</ul>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(MainMenuSlide3Sublist);
