import React from 'react';
import { connect } from "react-redux"
import heroImg from "../../../static/images/slide3/chapter-3/hero.jpg"
import S3Ch3List from "./chapter-3/List"

import { useRef, useEffect } from "react";
import Rellax from "rellax";

const Section3Chapter3 = (props) => {
	
	const heroImgDecoRef = useRef();
	const heroImgRef = useRef();
	
	useEffect(() => {
		const wrapper = ".slide-content"
		new Rellax(heroImgDecoRef.current, {
			speed: 1,
			round: true,
			wrapper: wrapper,
			horizontal: true
		});
		
		new Rellax(heroImgRef.current, {
			speed: 5,
			round: true,
			wrapper: wrapper,
			horizontal: true
		});
	}, []);
	
	const currentChapterClassName = (props.appUi.slide3CurrentChapter === props.chapterId) ? " slide-3__chapter--current" : " "
	
	return (
		<div className={
			"slide-3__chapter slide-3__chapter-3" + currentChapterClassName
		}>
			<div className="
					slide-content__section
					chapter__hero"
			>
				<h2 className="reveal--to-left">
					 <br /> - <br />  - <br />  
				</h2>
				
				<div className="chapter-hero__info reveal--to-left">
					<p>
						           
						 ,   -  
					</p>
					
					<p>
						        ,   
						     .
					</p>
				</div>
				
				<div className="chapter-hero__image reveal--fade">
					<div className="chapter-hero__img-wrapper">
						<img
							ref={ heroImgRef }
							src={ heroImg }
							alt="Illustration"
						/>
					</div>
					
					<div
						ref={ heroImgDecoRef }
						className="chapter-hero__image__deco-2"
					/>
				</div>
			</div>
			
			<S3Ch3List />
		</div>
	)
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Section3Chapter3);
