import React from 'react';

// images
import dir1 from "../../../../static/images/slide3/chapter-1/dir-1.jpg"
import dir2 from "../../../../static/images/slide3/chapter-1/dir-2.jpg"
import dir3 from "../../../../static/images/slide3/chapter-1/dir-3.jpg"
import dir4 from "../../../../static/images/slide3/chapter-1/dir-4.jpg"
import OneSvg from "../svg-numbers/OneSvg"
import TwoSvg from "../svg-numbers/TwoSvg"
import ThreeSvg from "../svg-numbers/ThreeSvg"
import FourSvg from "../svg-numbers/FourSvg"

const S3Ch1Section1 = (props) => {
	return (
		<div
			className="
				slide-content__section
				s3-ch1__directions"
		>
			<h3 className="reveal--to-left"> </h3>
			
			<div className="s3-ch1__directions-list ">
				<div className="dir-list__item reveal--to-left">
					<div className="dir-list__visual">
						<OneSvg />
						<img className="dir__cover" src={ dir3 } alt="Cover" />
					</div>
					
					<h4>          </h4>
				</div>
				
				<div className="dir-list__item reveal--to-left">
					<div className="dir-list__visual">
						<TwoSvg />
						<img className="dir__cover" src={ dir1 } alt="Cover" />
					</div>
					
					<h4></h4>
				</div>
				
				<div className="dir-list__item reveal--to-left">
					<div className="dir-list__visual">
						<ThreeSvg />
						<img className="dir__cover" src={ dir2 } alt="Cover" />
					</div>
					
					<h4>  , </h4>
				</div>
				
				<div className="dir-list__item reveal--to-left">
					<div className="dir-list__visual">
						<FourSvg />
						<img className="dir__cover" src={ dir4 } alt="Cover" />
					</div>
					
					<h4>    </h4>
				</div>
			</div>
		</div>
	);
};

export default S3Ch1Section1;
