import React from 'react';
import { connect } from "react-redux"

const Slide3NavIndicator = (props) => {
	
	const indicatorStyles = () => {
		switch (props.appUi.slide3CurrentChapter) {
			case 1:
				return {
					height: "calc(24% - 16px)",
					top: "calc(0% + 8px)"
				}
			case 2:
				return {
					height: "calc(24% - 16px)",
					top: "calc(24% + 8px)"
				}
			case 3:
				return {
					height: "calc(32% - 16px)",
					top: "calc(48% + 8px)"
				}
			case 4:
				return {
					height: "calc(20% - 16px)",
					top: "calc(80% + 8px)"
				}
			default:
				return {
					height: "calc(24% - 16px)",
					top: "calc(0% + 8px)"
				}
		}
	}
	
	return (
		<div
			className="s3__nav-indicator"
			style={ indicatorStyles() }
		/>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide3NavIndicator);