import React from 'react';
import { connect } from "react-redux"
import heroImg from "../../../static/images/slide3/chapter-2/hero.jpg"
import S3Ch2Section1 from "./chapter-2/Section1"
import S3Ch2Section2 from "./chapter-2/Section2"

import { useRef, useEffect } from "react";
import Rellax from "rellax";

const Section3Chapter2 = (props) => {
	
	const heroImgDecoRef = useRef();
	const heroImgRef = useRef();
	
	useEffect(() => {
		const wrapper = ".slide-content"
		new Rellax(heroImgDecoRef.current, {
			speed: 1,
			round: true,
			wrapper: wrapper,
			horizontal: true
		});
		
		new Rellax(heroImgRef.current, {
			speed: 5,
			round: true,
			wrapper: wrapper,
			horizontal: true
		});
	}, []);
	
	const currentChapterClassName = (props.appUi.slide3CurrentChapter === props.chapterId) ? " slide-3__chapter--current" : " "
	
	return (
		<div className={
			"slide-3__chapter slide-3__chapter-2" + currentChapterClassName
		}>
			<div className="
					slide-content__section
					chapter__hero"
			>
				<h2 className="reveal--to-left">
					  <br />
					 
				</h2>
				
				<div className="chapter-hero__info reveal--to-left">
					<p>
						         . 
						           
						  .
					</p>
					
					<p>
						          
						   .
					</p>
				</div>
				
				<div className="chapter-hero__image chapter-hero__image--top reveal--fade">
					<div className="chapter-hero__img-wrapper">
						<img
							ref={ heroImgRef }
							src={ heroImg }
							alt="Illustration"
						/>
					</div>
					
					<div
						ref={ heroImgDecoRef }
						className="chapter-hero__image__deco-2"
					/>
				</div>
			</div>
			
			{/* Section 1	*/ }
			<S3Ch2Section1 />
			
			{/* Section 2	*/ }
			<S3Ch2Section2 />
		
		</div>
	)
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Section3Chapter2);
