import React from 'react';
import { connect } from "react-redux"
import Section3Navigation from "./slide3/Navigation"
import Section3Chapter1 from "./slide3/Chapter1"
import Section3Chapter2 from "./slide3/Chapter2"
import Section3Chapter3 from "./slide3/Chapter3"
import Section3Chapter4 from "./slide3/Chapter4"

// images
// import image_name from "../../static/images/slide3/"

const Slide3 = (props) => {
	
	const activeClassName = (props.appUi.currentSlideId === props.slideIndex) ? " slide--current" : " "
	
	return (
		<div
			className={
				"slide-content__slide content__slide-3" + activeClassName
			}
		>
			
			<Section3Navigation />
			{/*SECTION 1*/ }
			<Section3Chapter1 chapterId={ 1 } />
			<Section3Chapter2 chapterId={ 2 } />
			<Section3Chapter3 chapterId={ 3 } />
			<Section3Chapter4 chapterId={ 4 } />
		
		</div>
	)
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide3);
