import React from 'react';
import { connect } from "react-redux"


const Slide4SectionNavVerticalLines = (props) => {
	
	const size = props.logistics[`s${ props.sectionIndex }Length`]
	
	const linesBlockStyles = {
		width: `calc(((100vw - 80px) / 3) * ${ size }`,
		transform: `translateX(calc(((100vw - 80px) / 3) * -1 * ${ props.logistics[`s${ props.sectionIndex }leftItemIndex`] - 1 } - 1px))`,
	}
	
	const renderVerticalLines = () => {
		let lines = []
		
		for (let i = 1; i < size; i++) {
			lines.push(i)
		}
		
		return lines.map((num, index) => <span key={ index } className="s4__nav-vertical-line" />)
	}
	
	return (
		<div
			className="section-nav__vertical-lines"
			style={ linesBlockStyles }
		>
			{ renderVerticalLines() }
			{ props.children }
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide4SectionNavVerticalLines);
