import React from 'react';
// oberon horizontal -- currently no need in this
// import { useState } from 'react';
// import useHorizontal from '@oberon-amsterdam/horizontal/hook';
//
import { connect } from "react-redux"
import Slide1 from "./content-pages/Slide1"
import Slide2 from "./content-pages/Slide2"
import Slide3 from "./content-pages/Slide3"
import Slide4 from "./content-pages/Slide4"


const SlideContent = (props) => {
	
	// Horizontal scroll section code
	// I'd prefer to use useRef, but we need a rerender to be triggered
	// const [container, setContainer] = useState();
	//
	// useHorizontal({
	// 	container: container,
	// 	showScrollbars: true,
	// 	scrollAmount: 150
	// });
	
	const wheelHandler = (e) => {
		//  4-      
		if (props.currentSlideId === 4) {
			return
		}
		
		const slideContent = document.querySelector('.slide-content')
		let prevScroll = slideContent.scrollLeft
		
		if (!e.shiftKey && !e.ctrlKey) {
			const isFirefox = navigator.userAgent.search("Firefox")
			
			// short and clean, but won't work in Firefox cuz of their synthetic scroll
			// slideContent.scrollLeft = prevScroll + e.deltaY
			
			
			if (isFirefox === -1) {
				slideContent.scrollLeft = prevScroll + e.deltaY
			}
			
			if (isFirefox > -1) {
				const viewportWidthStep = window.innerWidth / 5.76576

				if (e.deltaY > 0) {
					slideContent.scrollLeft = prevScroll + viewportWidthStep
				} else if (e.deltaY < 0) {
					slideContent.scrollLeft = prevScroll - viewportWidthStep
				}
			}
			
			// console.clear()
			//
			// console.log('Is Chrome:', navigator.userAgent.search("Chrome"))
			// console.log('Is Firefox:', navigator.userAgent.search("Firefox"))
		}
	}
	
	const keyDownHandle = (e) => {
		const slideContent = document.querySelector('.slide-content')
		let prevScroll = slideContent.scrollLeft
		
		const viewportWidth = window.innerWidth
		
		// pageUp
		if (e.keyCode === 33) {
			prevScroll += -viewportWidth
			
			slideContent.scrollTo({left: prevScroll, behavior: "smooth"})
		}
		// pageDown
		if (e.keyCode === 34) {
			prevScroll += viewportWidth
			
			slideContent.scrollTo({left: prevScroll, behavior: "smooth"})
		}
		
		// // arrowUp
		// if (e.keyCode === 38) {
		// 	prevScroll += -150
		//
		// 	slideContent.scrollTo({left: prevScroll, behavior: "smooth"})
		// }
		// // arrowDown
		// if (e.keyCode === 40) {
		// 	prevScroll += 150
		//
		// 	slideContent.scrollTo({left: prevScroll, behavior: "smooth"})
		// }
	}
	
	const wrapperSlide4Class = (props.currentSlideId === 4) ? " overflow-x--hidden" : ""
	
	return (
		<div
			className="slide-content"
			// ref={ ref => {
			// 	setContainer(ref)
			// } }
			onWheel={ wheelHandler }
			onKeyDown={ keyDownHandle}
			tabIndex="0"
		>
			<div className={"slide-content--wrapper" + wrapperSlide4Class}>
				<Slide1 slideIndex={ 1 } />
				<Slide2 slideIndex={ 2 } />
				<Slide3 slideIndex={ 3 } />
				<Slide4 slideIndex={ 4 } />
			</div>
		
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(SlideContent);
