import React from 'react';

// images
import item1 from "../../../../static/images/slide3/chapter-4/list-1.jpg"
import item2 from "../../../../static/images/slide3/chapter-4/list-2.jpg"
import item3 from "../../../../static/images/slide3/chapter-4/list-3.jpg"
import item4 from "../../../../static/images/slide3/chapter-4/list-4.jpg"
import OneSvg from "../svg-numbers/OneSvg"
import FourSvg from "../svg-numbers/FourSvg"
import ThreeSvg from "../svg-numbers/ThreeSvg"
import TwoSvg from "../svg-numbers/TwoSvg"


const S3Ch4ListGrid = (props) => {
	return (
		<div className="s3-ch4__list">
			<div className="list-title">
				<h2 className="reveal--fade"> <br/> </h2>
			</div>
			
			
			{/* List of actual grid-items	*/ }
			{/* Item 1 */ }
			<div className="item item--1">
				<div className="item__img">
					<img className="reveal--to-left" src={ item1 } alt="1" />
				</div>
				
				<div className="item__content reveal--to-left">
					<OneSvg />
					
					<p>
						 (),   
					</p>
				</div>
			</div>
			
			{/* Item 2 */ }
			<div className="item item--2">
				<div className="item__img">
					<img className="reveal--to-left" src={ item2 } alt="2" />
				</div>
				
				<div className="item__content reveal--to-left">
					<TwoSvg />
					
					<p>
						       
					</p>
				</div>
			</div>
			
			{/* Item 3 */ }
			<div className="item item--3">
				<div className="item__img">
					<img className="reveal--to-left" src={ item3 } alt="3" />
				</div>
				
				<div className="item__content reveal--to-left">
					<ThreeSvg />
					
					<p>
						     
					</p>
				</div>
			</div>
			
			{/* Item 4 */ }
			<div className="item item--4">
				<div className="item__img">
					<img className="reveal--to-left" src={ item4 } alt="4" />
				</div>
				
				<div className="item__content reveal--to-left">
					<FourSvg />
					
					<p>
						       
					</p>
				</div>
			</div>
		</div>
	);
};

export default S3Ch4ListGrid;
