import React from 'react';
import { connect } from "react-redux"

const PageLogo = (props) => {
	const pageLogoClassList = props.menuOpen ? "page-logo page-logo--fixed" : "page-logo"
	
	return (
		<div className={ pageLogoClassList }>
			<a href="/">
			</a>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(PageLogo);
