import React from 'react';
import { connect } from "react-redux"

const Slide4SectionNavItem = (props) => {
	const extraClass = (props.itemIndex === props.logistics[`s${ props.sectionIndex }CurrentItem`]) ? " s4__list-item--active" : ""
	
	return (
		<>
		<div className={ "s4__list-item" + extraClass }>
			<span>{ props.itemText }</span>
		</div>
		
		{/*<span className="s4__nav-vertical-line" />*/}
		</>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide4SectionNavItem);