import React from 'react';
import { connect } from "react-redux"
import heroDualImg from "../../../static/images/slide3/chapter-4/hero-dual.jpg"
import S3Ch4ListGrid from "./chapter-4/List"

import { useRef, useEffect } from "react";
import Rellax from "rellax";


const Section3Chapter4 = (props) => {
	
	const heroImgDecoRef = useRef();
	const heroImgRef = useRef();
	const dualTopImgRef = useRef();
	
	const rellaxFunc = (elementRef, speed) => {
		new Rellax(elementRef.current, {
			speed: speed,
			round: false,
			wrapper: ".slide-content",
			horizontal: true
		});
	}
	
	useEffect(() => {
		rellaxFunc(heroImgDecoRef, 1)
		rellaxFunc(heroImgRef, 3)
		rellaxFunc(dualTopImgRef, 3)
	}, []);
	
	const currentChapterClassName = (props.appUi.slide3CurrentChapter === props.chapterId) ? " slide-3__chapter--current" : " "
	
	return (
		<div className={
			"slide-3__chapter slide-3__chapter-4" + currentChapterClassName
		}>
			<div className="
					slide-content__section
					chapter__hero"
			>
				<h2 className="reveal--to-left">
					 
				</h2>
				
				<div className="chapter-hero__info reveal--to-left">
					<p>
						        
						 :
					</p>
					
					<ul className="custom-ul">
						<li>
							 
							
							<ul className="custom-ul custom-ul--skeleton">
								<li> </li>
								<li> </li>
								<li> </li>
								<li>  </li>
							</ul>
						</li>
						
						<li> </li>
						<li> - </li>
					</ul>
				</div>
				
				<div className="chapter-hero__image reveal--fade">
					<div className="chapter-hero__img-wrapper">
						<img
							ref={ heroImgRef }
							src={ heroDualImg }
							alt="Illustration"
						/>
					</div>
					
					<div
						ref={ heroImgDecoRef }
						className="chapter-hero__image__deco-2"
					/>
				</div>
			</div>
			
			{/* DECO	*/ }
			<div className="s3-ch4__deco">
				<img
					ref={ dualTopImgRef }
					src={ heroDualImg }
					alt=""
				/>
			</div>
			{/* LIST	*/ }
			<S3Ch4ListGrid />
		</div>
	)
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Section3Chapter4);
