import React from 'react';
import { connect } from "react-redux"

const DescriptionContent = (props) => {
	const currentDescription = (props.descriptionId === props.currentSlideId) ? ' current-description' : ''
	
	const titleFz31 = props.fz31 ? "fz-31" : ""
	
	if (props.descriptionId === props.currentSlideId) {
		return (
			<div
				className={ "slide-description__content" + currentDescription }
			>
				<h1
					className={ titleFz31 }
				>{ props.heading }</h1>
				<p>{ props.paragraph }</p>
			</div>
		);
	} else {
		return (
			<></>
		)
	}
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(DescriptionContent);
