import React from 'react';
import { connect } from "react-redux"
import Slide3NavNavItem from "./NavItem"
import Slide3NavIndicator from "./NavIndicator"
import Slide3NavHorizontalLines from "./HorizontalLines"

const Section3Navigation = (props) => {
	return (
		<div className="slide-content__section slide-3__nav reveal--fade">
			<div className="s3__nav-wrapper">
				<Slide3NavNavItem
					index={ 1 }
					text={ props.slidesData.slide3.services.chapter1 }
				/>
				
				<Slide3NavNavItem
					index={ 2 }
					text={ props.slidesData.slide3.services.chapter2 }
				/>
				
				<Slide3NavNavItem
					index={ 3 }
					text={ props.slidesData.slide3.services.chapter3 }
				/>
				
				<Slide3NavNavItem
					index={ 4 }
					text={ props.slidesData.slide3.services.chapter4 }
				/>
				
				<Slide3NavIndicator />
				
				<Slide3NavHorizontalLines />
			</div>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Section3Navigation);
