import React from 'react';
import { connect } from "react-redux"

const MainScreenBgVideo = (props) => {
	const bgVideoActive = (props.videoIndex === props.currentSlideId) ? " active-bg-video" : ""
	
	return (
		<div className={ "main-screen__bg-video" + bgVideoActive }>
			<video
				loop
				autoPlay
				muted
				data-videoid={ props.videoIndex }
				key={ props.videoSrc }
				poster={ props.videoPoster }
			>
				<source src={ props.videoSrc } />
			</video>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appData,
	...state.appUi
})

export default connect(mapStateToProps)(MainScreenBgVideo);
