import React from 'react';
import MainScreenBgVideo from "./MainScreen/MainScreenBgVideo"
import PageLogo from "./MainScreen/PageLogo"
import MainScreenLeftSide from "./MainScreen/MainScreenLeftSide"
import MainScreenRightSide from "./MainScreen/MainScreenRightSide"
import { connect } from "react-redux"
import { changeSlide } from "../redux/actions/appUiActions"
import ReactScrollWheelHandler from "react-scroll-wheel-handler";

const MainScreen = (props) => {
	
	const handleUpSlide = () => {
		if (props.currentSlideId !== 1) {
			const prevSlideId = props.currentSlideId - 1
			props.dispatch(changeSlide(prevSlideId))
		} else if (props.currentSlideId === 1) {
			props.dispatch(changeSlide(4))
		}
	}
	
	const handleDownSlide = () => {
		if (props.currentSlideId !== 4) {
			const nextSlideId = props.currentSlideId + 1
			props.dispatch(changeSlide(nextSlideId))
		} else if (props.currentSlideId === 4) {
			props.dispatch(changeSlide(1))
		}
	}
	
	return (
		<ReactScrollWheelHandler
			style={ {
				flexShrink: 0,
				width: '100%',
			} }
			className="main-screen"
			upHandler={ handleUpSlide }
			leftHandler={ handleUpSlide }
			downHandler={ handleDownSlide }
			rightHandler={ handleDownSlide }
			disableSwipe={ true }
			pauseListeners={ props.currentViewport === "slideContent" }
		>
			<PageLogo />
			
			<MainScreenLeftSide />
			
			<MainScreenRightSide />
			
			<MainScreenBgVideo
				videoIndex={ 1 }
				videoSrc={ props.slide1.video }
				videoPoster={ props.slide1.poster }
			/>
			<MainScreenBgVideo
				videoIndex={ 2 }
				videoSrc={ props.slide2.video }
				videoPoster={ props.slide2.poster }
			/>
			<MainScreenBgVideo
				videoIndex={ 3 }
				videoSrc={ props.slide3.video }
				videoPoster={ props.slide3.poster }
			/>
			<MainScreenBgVideo
				videoIndex={ 4 }
				videoSrc={ props.slide4.video }
				videoPoster={ props.slide4.poster }
			/>
		</ReactScrollWheelHandler>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi,
	...state.appData
})

export default connect(mapStateToProps)(MainScreen);
