import React from 'react';
import { connect } from "react-redux"
import ReactScrollWheelHandler from "react-scroll-wheel-handler";
import Slide4SectionNavItem from "./NavItem"
import Slide4SectionNavIndicator from "./NavIndicator"
import {
	navEnabledToggle,
	sectionNavMoveLeft,
	sectionNavMoveRight,
	sectionNavNextItem,
	sectionNavPrevItem,
	slide4SectionNext,
	slide4SectionPrev
} from "../../../redux/actions/logisticsUiActions"
import Slide4SectionNavVerticalLines from "./NavVerticalLines"
import Slide4ItemImgDefault from "./ItemImgDefault"

import img1 from '../../../static/images/slide4/section-1/item-1.png'
import img2 from '../../../static/images/slide4/section-1/item-2.png'
import img3 from '../../../static/images/slide4/section-1/item-3.png'


const Slide4Section1 = (props) => {
	const slideSectionUpHandler = (e) => {
		// console.log("UP")
		
		if (props.logistics.navEnabled === false) {
			return
		}
		props.dispatch(navEnabledToggle())

		setTimeout(() => {
			props.dispatch(navEnabledToggle())
		}, 700)
		
		if ((props.logistics[`s${ props.sectionIndex }CurrentItem`] === 1) && (props.logistics.currentSection > 1)) {
			props.dispatch(slide4SectionPrev())
			
			return
		}
		
		if (props.logistics[`s${ props.sectionIndex }CurrentItem`] > 1) {
			if (props.logistics[`s${ props.sectionIndex }CurrentItem`] === props.logistics[`s${ props.sectionIndex }leftItemIndex`]) {
				props.dispatch(sectionNavMoveLeft(props.sectionIndex))
			}
			
			props.dispatch(sectionNavPrevItem(props.sectionIndex))
		}
	}
	
	const slideSectionDownHandler = (e) => {
		// console.log("DOWN")
		
		if (props.logistics.navEnabled === false) {
			return
		}
		props.dispatch(navEnabledToggle())

		setTimeout(() => {
			props.dispatch(navEnabledToggle())
		}, 700)
		
		if ((props.logistics[`s${ props.sectionIndex }CurrentItem`] === props.logistics[`s${ props.sectionIndex }Length`]) && (props.logistics.currentSection < props.logistics.sectionsAmount)) {
			props.dispatch(slide4SectionNext())
			
			return
		}
		
		if (props.logistics[`s${ props.sectionIndex }CurrentItem`] < props.logistics[`s${ props.sectionIndex }Length`]) {
			if (props.logistics[`s${ props.sectionIndex }CurrentItem`] === props.logistics[`s${ props.sectionIndex }rightItemIndex`]) {
				props.dispatch(sectionNavMoveRight(props.sectionIndex))
			}
			
			props.dispatch(sectionNavNextItem(props.sectionIndex))
		}
	}
	
	const navListPositionStyles = {
		transform: `translateX(calc(((100vw - 80px) / 3) * -1 * ${ props.logistics[`s${ props.sectionIndex }leftItemIndex`] - 1 } - 1px))`,
	}
	
	return (
		<ReactScrollWheelHandler
			className="s4-section s4-section--1"
			upHandler={ slideSectionUpHandler }
			downHandler={ slideSectionDownHandler }
			disableKeyboard={ true }
		>
			<h2 className="reveal--to-left"> </h2>
			
			<div className="img__deco--large" />
			<div className="img__deco--small reveal--to-left" />
			
			<Slide4ItemImgDefault
				image={ img1 }
				imageIndex={ 1 }
				sectionIndex={ props.sectionIndex }
			/>
			
			<Slide4ItemImgDefault
				image={ img2 }
				imageIndex={ 2 }
				sectionIndex={ props.sectionIndex }
			/>
			
			<Slide4ItemImgDefault
				image={ img3 }
				imageIndex={ 3 }
				sectionIndex={ props.sectionIndex }
			/>
			
			<p className="reveal--to-left">
				         .   :   , -  ,   (),  (),  , ,    .
			</p>
			
			{/* NAVIGATION	*/ }
			<div className="s4__section-navigation">
				<div className="nav-header">
					<span></span>
					
					<div className="nav-buttons">
						<button
							className="nav__button button-prev"
							onClick={ slideSectionUpHandler }
						>
							Prev
						</button>
						
						<button
							className="nav__button button-next"
							onClick={ slideSectionDownHandler }
						>
							Next
						</button>
					</div>
				</div>
				
				<div className="section-nav__main reveal--fade">
					<div
						className="nav-list"
						style={ navListPositionStyles }
					>
						<Slide4SectionNavItem
							itemIndex={ 1 }
							sectionIndex={ props.sectionIndex }
							itemText={ " " }
						/>
						<Slide4SectionNavItem
							itemIndex={ 2 }
							sectionIndex={ props.sectionIndex }
							itemText={ "  " }
						/>
						<Slide4SectionNavItem
							itemIndex={ 3 }
							sectionIndex={ props.sectionIndex }
							itemText={ ",    " }
						/>
					</div>
					
					<Slide4SectionNavVerticalLines
						sectionIndex={ props.sectionIndex }
						lenght={ props.logistics[`s${ props.sectionIndex }Length`] }
					/>
					
					<Slide4SectionNavIndicator
						sectionIndex={ props.sectionIndex }
					/>
				</div>
			</div>
		
		</ReactScrollWheelHandler>
	);
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide4Section1);