import React from 'react';
import SliderIndicator from "./SliderIndicator"
import { changeSlide } from "../../../redux/actions/appUiActions"
import { connect } from "react-redux"

const SliderNavigation = (props) => {
	const prevBtnClickHandler = (e) => {
		if (props.currentSlideId !== 1) {
			const prevSlideId = props.currentSlideId - 1
			props.dispatch(changeSlide(prevSlideId))
		} else if (props.currentSlideId === 1) {
			props.dispatch(changeSlide(4))
		}
	}
	
	const nextBtnClickHandler = (e) => {
		if (props.currentSlideId !== 4) {
			const nextSlideId = props.currentSlideId + 1
			props.dispatch(changeSlide(nextSlideId))
		} else if (props.currentSlideId === 4) {
			props.dispatch(changeSlide(1))
		}
	}
	
	return (
		<div className="slider__navigation">
			<SliderIndicator />
			
			<div className="slider__nav-buttons">
				<button
					className="slider__nav-btn nav-btn__prev"
					onClick={ prevBtnClickHandler }
				>
					Prev
				</button>
				
				<button
					className="slider__nav-btn nav-btn__next"
					onClick={ nextBtnClickHandler }
				>
					Next
				</button>
			</div>
		</div>
	);
};

const mapStateToProps = (state) => ({
	...state.appUi
})

export default connect(mapStateToProps)(SliderNavigation);
